    padding-bottom: 48px;
}

.py-6 {
    padding-top: 60px;
    padding-bottom: 60px;
}

.py-7 {
    padding-top: 72px;
    padding-bottom: 72px;
}

.px-1 {
    padding-left: 4px;
    padding-right: 4px;
}

.px-2 {
    padding-left: 8px;
    padding-right: 8px;
}

.px-3 {
    padding-left: 16px;
    padding-right: 16px;
}

.px-4 {
    padding-left: 24px;
    padding-right: 24px;
}

.px-5 {
    padding-left: 48px;
