}

// section
.main-holder {
    overflow-x: hidden;
}

.section-wrapper {
    padding: 80px 0;
}

.sc-title {
    text-transform: uppercase;
    margin-bottom: 4.8rem;
    font-weight: bold;
    letter-spacing: 4px;
    position: relative;
    font-size: 24px;
    padding: 4px 0;
    position: relative;

    &::after {
        content: "";
        position: absolute;
        top: 100%;
        left: 0;
        height: 4px;
        width: 80px;
        background-color: $clr-orange;
    }
}

// opacity
.op {
    &-01 {
        opacity: 0.1;
    }

    &-02 {
        opacity: 0.2;
    }

    &-03 {
        opacity: 0.3;
    }

    &-04 {
        opacity: 0.4;
    }

    &-05 {
        opacity: 0.5;
    }

    &-06 {
        opacity: 0.6;
    }

    &-07 {
        opacity: 0.7;
    }

    &-08 {
        opacity: 0.8;
    }

    &-09 {
        opacity: 0.9;
    }
}

.breadcrumb {
    box-shadow: rgba(99, 99, 99, 0.1) 0px 2px 8px 0px;
    padding: 1rem 2rem;
    margin-bottom: 3.2rem;

    li {
        transition: $transition;

        &:hover {
            opacity: 0.9;
        }
    }
}

.vert-line {
    background: rgba(255, 255, 255, .4);
    height: 15px;
    width: 2px;
    margin: 0 10px;
}


.title-md {
    background-color: $clr-white;
    position: relative;
    box-shadow: 0 3px 10px rgb(0 0 0 / 0.2);
    padding: 3rem 5rem;
    text-transform: uppercase;
    color: $clr-gray;
    letter-spacing: 1px;
    width: 90%;
    margin-top: 5rem;

    span {
        text-transform: uppercase;
    }
    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 1rem;
        background-color: $clr-orange;
    }

}
@media screen and (max-width:576px) {
    .title-md {

        padding: 1.5rem 3rem;
    }
}